
import { defineComponent, PropType } from 'vue';
import Card from '../types/Card';

export default defineComponent({
	name: 'Content',
	emits: ['removeItem'],
	props: {
		cards: {
			type: Array as PropType<Card[]>,
			required: true
		}
	},
	methods: {
		removeItem(id: number) {
			this.$emit('removeItem', id);
		}
	}
});
